/*-----------------------------------------Banner--------------------------------*/
.banner {
  position: relative;
  height: 100%;
}

.blurry-bg {
  width: 100%;
  height: 100%;
  background-image: url(/static/media/banner-test.66b57e8d.jpg);
  position: absolute;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -1;
}

.banner .info{
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
}

.banner .tagline {
  font-weight: 700;
  letter-spacing: 0.8px;
  padding: 8px 10px;
  background: linear-gradient(90.21deg, #202A44  -5.91%, #393D47 111.58%);
  border: 1px solid rgba(255, 255, 255, 0.5);
  font-size: 20px;
  margin-bottom: 16px;
  display: inline-block;
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0, 0.4); /* Black w/opacity/see-through */
}
.banner h1 {
  font-size: 35px;
  font-weight: 700;
  letter-spacing: 0.8px;
  line-height: 1;
  margin-bottom: 20px;
  display: block;
}
.banner p {
  color: #B8B8B8;
  font-size: 18px;
  letter-spacing: 0.8px;
  line-height: 1.5em;
  width: 96%;
  font-weight: 600;
}
.banner button {
  color: white;
  font-weight: 600;
  font-size: 20px;
  margin-top: 60px;
  letter-spacing: 0.8px;
  display: flex;
  align-items: center;
  background: transparent;
  border-color: white;
}

.banner button:hover {
  background-color: #fff;
  color: #202A44;
  transition: 0.5s ease-in-out;
  -webkit-transform: scale(1);
          transform: scale(1);
}

.banner button svg {
  font-size: 25px;
  margin-left: 10px;
  transition: 0.3s ease-in-out;
  line-height: 1;
  color: white
}

.banner button:hover svg {
  font-size: 25px;
  margin-left: 25px;
  transition: 0.3s ease-in-out;
  line-height: 1;
  color: #202A44
}

.banner img {
  width: 100%;
  height:auto;
  -webkit-animation: updown 3s linear infinite;
          animation: updown 3s linear infinite;
  z-index: -1;
}
@-webkit-keyframes updown {
    0% {
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }
    50% {
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
    }
    100% {
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }
}
@keyframes updown {
    0% {
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }
    50% {
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
    }
    100% {
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }
}
.txt-rotate > .wrap {
  border-right: 0.08em solid #666;
}

/*------------------------Skills---------------------*/
.skill {
